import { Button, Tooltip } from '@mui/material';
import { makeStyles } from '@mui/styles';
import React, { FunctionComponent } from 'react';
import { useSnackbar } from 'notistack';
import { useSafeIntl } from '../localization/useSafeIntl';
import { commonStyles } from '../styles/iaso/common';
import MESSAGES from './messages';

const useStyles = makeStyles(theme => ({
    ...commonStyles(theme),
    tooltip: {
        marginRight: `${theme.spacing()} !important`,
    },
    textarea: {
        position: 'absolute',
        top: -5000,
        left: -5000,
        zIndex: -100,
    },
    errorMessage: {
        display: 'flex',
        overflow: 'hidden',
        whiteSpace: 'pre-wrap',
    },
}));

type Props = { errorLog?: Record<string, any> | string; id?: any };

export const SnackBarErrorMessage: FunctionComponent<Props> = ({
    errorLog = null,
    id = null,
}) => {
    const classes = useStyles();
    const { formatMessage } = useSafeIntl();
    const { closeSnackbar } = useSnackbar();
    if (!errorLog || errorLog === '') return null;

    let errorMessage;
    if (typeof errorLog === 'string') {
        errorMessage = errorLog;
    } else if (errorLog?.name === 'ApiError' || errorLog?.name === 'Error') {
        // Bypass a strange bug in stringify that remove the message from Error
        errorMessage = JSON.stringify(
            { ...errorLog, message: errorLog?.message },
            null,
            2,
        );
    } else {
        errorMessage = JSON.stringify(errorLog, null, 1);
    }

    const handleClick = e => {
        navigator.clipboard.writeText(errorMessage);
        e.target.focus();
    };

    const handleClose = () => closeSnackbar(id);
    return (
        <>
            <Tooltip
                title={
                    <pre className={classes.errorMessage}>{errorMessage}</pre>
                }
                className={classes.tooltip}
                arrow
            >
                <Button
                    onClick={e => handleClick(e)}
                    size="small"
                    variant="outlined"
                    color="inherit"
                >
                    {formatMessage(MESSAGES.copyError)}
                </Button>
            </Tooltip>
            <Button
                onClick={() => handleClose()}
                size="small"
                variant="outlined"
                color="inherit"
            >
                {formatMessage(MESSAGES.close)}
            </Button>
            <textarea
                onChange={() => null}
                className={classes.textarea}
                value={errorMessage}
            />
        </>
    );
};

export default SnackBarErrorMessage;
